import {createStore} from 'vuex'

export default createStore({
    state: () => ({
        currentSongDetail: localStorage.getItem('currentSongDetail') ?
            JSON.parse(localStorage.getItem('currentSongDetail'))
            : null,
        playList: localStorage.getItem('playList') ?
            JSON.parse(localStorage.getItem('playList'))
            : [],
        paused: true,
        currentTime: 0,
    }),
    mutations: {
        DBCLICK_SONG(state, payload) {
            this.commit('ADD_TO_PLAYLIST', payload)
            state.currentSongDetail = payload
            localStorage.setItem('currentSongDetail', JSON.stringify(state.currentSongDetail))
        },
        ADD_TO_PLAYLIST(state, payload) {
            let existed = false
            for (let song of state.playList) {
                if (song.id === payload.id) {
                    existed = true
                    break
                }
            }
            if (!existed) {
                state.playList.push(payload)
            }
            if (state.playList == []) {
                state.currentSongDetail = payload
            }
            localStorage.setItem('playList', JSON.stringify(state.playList))
        },
        REMOVE_FROM_PLAYLIST(state, payload) {
            let {playList, currentSongDetail} = state
            for (let i = 0; i < playList.length; ++i) {
                if (playList[i].id == payload.id) {
                    // 如果删除的是当前播放
                    state.playList.splice(i, 1)
                    localStorage.setItem('playList', JSON.stringify(state.playList))
                    if (currentSongDetail.id == payload.id) {
                        if (state.playList.length > 0) {
                            state.currentSongDetail = state.playList[0]
                        } else {
                            state.currentSongDetail = null
                        }
                        localStorage.setItem('currentSongDetail', JSON.stringify(state.currentSongDetail))
                    }
                    break
                }
            }
        },
        ADD_SONGLIST_TO_PLAYLIST(state, payload) {
            let set = new Set(state.playList.map((item) => item.id));
            for (let song of payload) {
                if (!set.has(song.id)) {
                    state.playList.push(song)
                }
            }
            localStorage.setItem('playList', JSON.stringify(state.playList))
        },
        PLAY_SONGLIST(state, payload) {
            state.currentSongDetail = payload[0]
            localStorage.setItem('currentSongDetail', JSON.stringify(state.currentSongDetail))
            state.playList = payload;
            localStorage.setItem('playList', JSON.stringify(state.playList))

        },
        CLEAR_PLAYLIST(state) {
            state.currentSongDetail = null;
            state.playList = [];
            localStorage.setItem('currentSongDetail', JSON.stringify(state.currentSongDetail))
            localStorage.setItem('playList', JSON.stringify(state.playList))
        },
        SET_CURRENT_SONG_DETAIL(state, payload) {
            state.currentSongDetail = payload;
            localStorage.setItem('currentSongDetail', JSON.stringify(state.currentSongDetail))
        },
        SET_PAUSED(state, payload) {
            state.paused = payload
        },
        SET_CURRENT_TIME(state, payload) {
            state.currentTime = payload
        }

    },
    actions: {},
    modules: {}
})
