import {createRouter, createWebHashHistory} from 'vue-router'


const routes = [
    {
        path: '',
        redirect: '/home/find-music/recommend'
    },
    // 主页
    {
        path: '/home',
        name: 'home',
        component: () => import('@/views/home/Home'),
        children: [
            // 发现音乐
            {
                path: '/home/find-music',
                name: 'find-music',
                component: () => import('@/views/home/findMusic/FindMusic'),
                children: [
                    // 个性推荐
                    {
                        path: '/home/find-music/recommend',
                        name: 'recommend',
                        component: () => import('@/views/home/findMusic/recommend/Recommend'),
                    },
                    // 歌单
                    {
                        path: '/home/find-music/find-song-list',
                        name: 'find-song-list',
                        component: () => import('@/views/home/findMusic/findSongList/FindSongList'),
                    },
                    // 排行榜
                    {
                        path: '/home/find-music/rank',
                        name: 'rank',
                        component: () => import('@/views/home/findMusic/rank/Rank'),
                    },
                    // 歌手
                    {
                        path: '/home/find-music/find-singer',
                        name: 'find-singer',
                        component: () => import('@/views/home/findMusic/findSinger/FindSinger'),
                    },
                    // 最新音乐
                    {
                        path: '/home/find-music/latest-music',
                        name: 'latest-music',
                        component: () => import('@/views/home/findMusic/latestMusic/LatestMusic'),
                    }

                ]
            },
            // 歌单详情模块
            {
                path: '/home/song-list/:id',
                component: () =>
                    import ('@/views/home/songList/SongList'),
            },

        ]
    },
    {path: '/404', component: () => import('@/views/NotFound')},
    {
        path: '/:pathMatch(.*)',
        redirect: '/404'
    },
    // {path: '/test', name: 'fuck', component: () => import('@/components/Test')},

]

const router = createRouter({
    history: createWebHashHistory(),
    routes
})

export default router
